___CSS_LOADER_EXPORT___.push([module.id, `/* generic website styles */

:root {
  --header-color: #000000;
  --nav-a-color: #ffffff;
  --nav-a-bg-mobile-devices-color: #563517;
  --nav-a-hover-text-color: #9c6f44;
  --content-separator-color: #9c6f44;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto, Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  padding: 0;
  margin: 0;
}

/* google icons */
.material-symbols-outlined {
  font-variation-settings: "FILL" 0, "wght" 400, "GRAD" 0, "opsz" 48;
  font-size: 3.2rem;
  color: #9c6f44;
}

#content {
  width: 100%;
  height: 100%;
  min-height: 100vh;
}

section {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  padding: 0 1rem;
}

section h2 {
  font-size: 3rem;
  text-transform: uppercase;
  padding: 1rem;
  font-family: "Dancing Script", cursive;
}

.second-column {
  display: flex;
  height: 100vh;
  flex-flow: column wrap;
  align-items: center;
  gap: 50px;
}

.separator {
  display: flex;
  justify-content: flex-start;
  width: 200px;
  height: 2px;
  background-color: var(--content-separator-color);
}

section p {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 400;
  letter-spacing: 0.2rem;
  font-size: 1.3rem;
  text-align: center;
}

/* header and nav styles */

.header {
  background-color: var(--header-color);
}

header {
  max-width: 1200px;
  padding: 1rem;
  margin: 0 auto;
}

#header-logo {
  max-width: 314px;
  max-height: 32px;
}

/* navigation styling across all the breakpoints */

nav ul {
  display: flex;
  gap: 0.5em;
  flex-flow: row wrap;
  justify-content: flex-end;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  /* I have selected 48% for flex basis because I want to have two li elements per line. */
  flex-basis: 48%;
  margin-bottom: 0.6rem;
}

nav li:first-child {
  /* in order for logo to be on his own line on mobile devices we set a flex-basis selector to 100% */
  flex-basis: 100%;
  text-align: center;
}

nav a {
  display: block;
  padding: 1rem;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1.2rem;
  color: var(--nav-a-color);
  background-color: var(--nav-a-bg-mobile-devices-color);
  text-align: center;
}

nav a:hover {
  color: var(--nav-a-hover-text-color);
}

nav li:first-child a {
  background-color: transparent;
}

/* contact section of the page */
#contact-container {
  display: flex;
  flex-flow: column wrap;
  align-content: center;
}

input {
  display: block;
}

label {
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 1rem;
  font-style: italic;
}

textarea {
  resize: none;
}

.form-container form {
  padding: 1rem;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
}

/* for tablet */
@media (min-width: 550px) {
  nav ul {
    justify-content: center;
    gap: 0.5em;
  }
  nav li {
    flex-basis: auto;
    margin-bottom: 0;
  }
  nav a {
    font-size: 1.2rem;
    color: var(--nav-a-color);
    background-color: transparent;
  }
  nav a:hover {
    background-color: transparent;
  }
}

/* for desktop */
@media (min-width: 800px) {
  /* navigation for logo in the center */
  nav ul {
    justify-content: flex-end;
    gap: none;
  }

  nav a {
    font-size: 1.5rem;
  }

  nav li:first-child {
    flex-basis: auto;
    margin: 0 auto;
    order: 2;
  }
  nav li:nth-child(2),
  nav li:nth-child(3) {
    order: 1;
  }
  nav li:nth-child(4),
  nav li:nth-child(5) {
    order: 3;
  }
}
